@import "form";

.container {
    margin: 0 auto;
    max-width: 1173px;
    padding: 0 10px;
}

button {
    background-color: #fff;
    border: 1px solid #39B54A;
    height: 36px;
    width: 146px;
    text-align: center;
    line-height: 36px;
    border-radius: 4px;
    align-items: center;
    color: #000000;
    &:hover {
        background-color: #39B54A;
        color: #fff;
    }
}

a {
    text-decoration: none;
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    height:100%;
}

.header {
    margin: 40px 0 22px 0;

    .header__wrapper {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-start;

        .list__title {
            font-family: Roboto, sans-serif;
            font-style: normal;
            font-weight: bold;
            font-size: 32px;
            line-height: 37px;
            display: flex;
            align-items: center;
        }

    }
}

table {
    margin-top: 22px;
    border-collapse: collapse;
    border-spacing: 0;
}

tr {
    height: 42px;
    /*display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;*/
    font-family: Tahoma, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 42px;
    border: 1px solid #E6E6E6;
    box-sizing: content-box;

    &:hover {
        background-color: rgba(71, 255, 100, 0.16);
    }

    td {
        margin: auto;
        border: 1px solid #E6E6E6;
    }


    .item__index {
        padding-left: 21px;

        width: 183px;
        a {
            height:100%;
            width:100%;
        }

    }

    .item__description {
        width: 867px;
        padding: 10px 10px 10px 20px;
    }

    .item__manipulation {
        width: 99px;
        padding-left: 20px;
        /* display: flex;*/
        color: #666666;
        font-weight: 200;
        font-size: 22px;

        .item__details {
            display: inline-block;
            cursor: pointer;

            &:hover {
                transform: scale(1.1)
            }
        }

        .item__delete {
            cursor: pointer;
            padding-left: 24px;
            color: #F4583F;
            display: inline-block;

            &:hover {
                transform: scale(1.1)
            }
        }
    }
}

//---------------Detail-----------------------------
.detail__main {
    margin-top: 12px;
    height: 40px;
    display:flex;
    align-items:center;
    width: 100%;
    background: #F2F2F2;
    border-radius: 4px;
    border: none;
    padding: 0 10px;
}

.detail__text {
    margin-top: 22px;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
}

.detail__title {
    font-family: Roboto, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 32px;
    line-height: 37px;
}
.detail-btn{
    margin-top:34px;
    width: 180px;
    border: 1px solid #0071BC;
    &:hover{
        background-color: #0071BC;
    }
}

//-----------------Modal----------------------------
.modal__created-blur {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    opacity: .7;
    background-color: #383838;
    z-index: 3;
}

.item__card {
    position: absolute;
    background-color: #ffffff;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid #F2F2F2;
    border-radius: 4px;
    max-width: 462px;
    min-height: 182px;
    width:100%;
    animation: fadeIn .3s ease-in-out;
    z-index: 4;
    padding: 32px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    .item__card-header {
        width: 100%;
        display: flex;
        justify-content: space-between;

        .item__card-title {
            font-family: Roboto, sans-serif;
            font-style: normal;
            font-weight: normal;
            font-size: 14px;
            line-height: 16px;
            display: flex;

        }

        .item__card-close {
            position: relative;
            width: 14px;
            height: 14px;
            cursor: pointer;

            &:before, &:after {
                position: absolute;
                content: '';
                width: 20px;
                height: 1px;
                background-color: #FF0000;
            }

            &:before {
                transform: rotate(-45deg);
            }

            &:after {
                transform: rotate(45deg);
            }
        }
    }

    form,.delete__card {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: flex-end;
        width: 100%;
        div {
            width: 100%;
            input {
                margin-top: 12px;
                height: 40px;
                width: 100%;
                background: #F2F2F2;
                border-radius: 4px;
                border: none;
                padding: 0 10px;
            }
        }

        .create-btn {
            margin-top: 20px;
            width: 148px;
        }
        .edit-btn{
            margin-top: 20px;
        }
        .delete-btn{
            border-color:#FF0000;;
            &:hover{
                background-color: #FF0000;;
            }
        }
    }
}




